function Upload(props) {
  const [ModalOpen, setModalOpen] = useState(false);
  const ismodalOpen = ModalOpen;

  var user = {
    userData: {
      isAuth: false,
    },
  };

  let state = useSelector((state) => state);

  if (state.user.userData) {
    user = state.user;
  }

  // useEffect(() => {
  //     setTimeout(()=>{
  //         alert("  .")
  //     }, 10000);
  // }, [ResizedFilePath, Thumbnail]);

  const uploadOnClickHandler = (event) => {
    setModalOpen(true);
    console.log('text . , ' + ModalOpen);
  };

  const overlayOnClickHandler = () => {
    setModalOpen(false);
  };

  ////////////////////////using Dropzone//////////////////////////

  const PrivateOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ];

  const [Title, setTitle] = useState('');
  const [Description, setDescription] = useState('');
  const [Private, setPrivate] = useState(0);
  const [Category, setCategory] = useState('Film & Animation');
  const [FilePath, setFilePath] = useState('');
  const [FileName, setFileName] = useState('');
  const [ResizedFilePath, setResizedFilePath] = useState('');
  const [Duration, setDuration] = useState('');
  const [Thumbnail, setThumbnail] = useState('');

  const onTitleChange = (e) => {
    setTitle(e.currentTarget.value);
  };

  const onDescriptionChange = (e) => {
    setDescription(e.currentTarget.value);
  };

  const onPrivateChange = (e) => {
    console.log(e);
    setPrivate(e);
  };

  const onCategoryChange = (e) => {
    console.log(e);
    setCategory(e);
  };

  const onSubmit = (event) => {
    event.preventDefault();

    const variables = {
      writer: user.userData._id,
      post_title: Title,
      post_content: Description,
      privacy: Private,
      category: Category,
      filePath: FilePath,
      duration: Duration,
      thumbnail: Thumbnail,
      resizedFilePath: ResizedFilePath,
    };

    Axios.post('/api/formDatas/fileUpload', variables).then((response) => {
      if (response.data.success) {
        // window.message.success("The file uploaded successfully..")
        alert('  .');
      } else {
        alert('  .');
      }
      setModalOpen(false);
    });
  };

  const onDrop = (files) => {
    // setFilePath('');
    // setThumbnail('');
    // setResizedFilePath('');
    const uploadFileAsyncFunc = async function () {
      let formData = new FormData();
      const config = {
        header: { 'content-type': 'multipart/form-data' },
      };

      let formData_filesAppended = await new Promise((resolve, reject) => {
        console.log('onDrop formData');
        console.log(files);
        // files.forEach((file, index)=>{
        //   formData.append('file', file);
        // })
        formData.append('file', files[0]);
        resolve(formData);
      });

      let log_formData = await new Promise((resolve, reject) => {
        console.log(formData_filesAppended);
        console.log(formData);
        resolve(formData);
      });

      // TEST
      let uploadDropedFileAtServer = await new Promise((resolve, reject) => {
        console.log(log_formData);
        Axios.post('/api/formDatas/uploadfiles', formData, config).then(
          (response) => {
            if (response.data.success) {
              setFilePath(response.data.filePath);
              setFileName(response.data.fileName);
              resolve(response.data);
            } else {
              alert('  .');
            }
          }
        );
      });

      let makeThumbOrResize = await new Promise((resolve, reject) => {
        console.log(uploadDropedFileAtServer);
        if (
          uploadDropedFileAtServer.fileName.match(/.mp4/) &&
          uploadDropedFileAtServer.fileName.endsWith('.mp4')
        ) {
          console.log(' .  .');

          Axios.post('/api/formDatas/thumbnails', {
            filePath: uploadDropedFileAtServer.filePath,
            fileName: uploadDropedFileAtServer.fileName,
          }).then((response) => {
            if (response.data.success) {
              setDuration(response.data.fileDuration);
              setThumbnail(response.data.thumbsFilePath);
              resolve('done');
            } else {
              alert('  . ');
            }
          });
        } else if (
          (uploadDropedFileAtServer.fileName.match(/.png/) &&
            uploadDropedFileAtServer.fileName.endsWith('.png')) ||
          (uploadDropedFileAtServer.fileName.match(/.PNG/) &&
            uploadDropedFileAtServer.fileName.endsWith('.PNG')) ||
          (uploadDropedFileAtServer.fileName.match(/.jpg/) &&
            uploadDropedFileAtServer.fileName.endsWith('.jpg')) ||
          (uploadDropedFileAtServer.fileName.match(/.jpeg/) &&
            uploadDropedFileAtServer.fileName.endsWith('.jpeg'))
        ) {
          console.log(
            '  .  .  .'
          );

          Axios.post('/api/formDatas/resizing', {
            filePath: uploadDropedFileAtServer.filePath,
            fileName: uploadDropedFileAtServer.fileName,
          }).then((response) => {
            if (response.data.success) {
              console.log('The file resized..');
              setResizedFilePath(
                `uploads\\resized-${uploadDropedFileAtServer.fileName}`.replace(
                  '\\',
                  '/'
                )
              );
              console.log(`resized file path : ${ResizedFilePath}`);
              resolve('done');
            } else {
              alert('resizing failed');
            }
          });
        } else {
          alert('   .');
          console.log(
            '  .   .    .'
          );
          resolve('done');
        }
      });
    };
    uploadFileAsyncFunc().then((value) => {
      console.log(value);
    });
    // ^ TEST ^

    // Axios.post('/api/formDatas/uploadfiles', formData, config).then(
    //   (response) => {
    //     if (response.data.success) {
    //       let variable = {
    //         filePath: response.data.filePath,
    //         fileName: response.data.fileName,
    //       };
    //       setFilePath(response.data.filePath);

    //       var ext = response.data.fileName;
    //       if (ext.match(/.mp4/) && ext.endsWith('.mp4')) {
    //         console.log(' .  .');

    //         Axios.post('/api/formDatas/thumbnails', variable).then(
    //           (response) => {
    //             if (response.data.success) {
    //               setDuration(response.data.fileDuration);
    //               setThumbnail(response.data.thumbsFilePath);
    //               console.log(
    //                 `http://${expressServerAddress}:${expressServerPort}/${Thumbnail}`
    //               );
    //             } else {
    //               alert('  . ');
    //             }
    //           }
    //         );
    //       } else if (
    //         (ext.match(/.png/) && ext.endsWith('.png')) ||
    //         (ext.match(/.PNG/) && ext.endsWith('.PNG')) ||
    //         (ext.match(/.jpg/) && ext.endsWith('.jpg')) ||
    //         (ext.match(/.jpeg/) && ext.endsWith('.jpeg'))
    //       ) {
    //         console.log(
    //           '  .  .  .'
    //         );
    //         Axios.post('/api/formDatas/resizing', variable).then((response) => {
    //           if (response.data.success) {
    //             console.log('The file resized..');
    //             setResizedFilePath(
    //               `uploads\\resized-${ext}`.replace('\\', '/')
    //             );
    //             console.log(`resized file path : ${ResizedFilePath}`);
    //           } else {
    //             alert('resizing failed');
    //           }
    //         });
    //       } else {
    //         alert('   .');
    //         console.log(
    //           '  .   .    .'
    //         );
    //       }
    //     } else {
    //       alert(' .');
    //     }
    //   }
    // );
  };

  return (
    <div className='div_uploadContainer'>
      {ModalOpen === true && (
        <div className='div_Modal'>
          <div className='div_Modal_'>
            <div className='div_ModalContainer'>
              <div
                className='div_ModalOverlay'
                onClick={() => {
                  overlayOnClickHandler();
                }}
              ></div>
            </div>

            <div className='div_ModalContents'>
              <div className='div_ModalContents_'>
                <div className='div_ModalContents_title'>
                  <span> </span>
                  <div className='div_ModalContents_title_close'></div>
                </div>

                <form onSubmit={onSubmit}>
                  <div className='div_ModalContents_body'>
                    <div className='div_ModalContents_body_header'>
                      <Avatar
                        style={{
                          margin: '4px',
                          cursor: 'pointer',
                          width: '40px',
                          height: '40px',
                        }}
                        src={
                          'http://scontent-nrt1-1.xx.fbcdn.net/v/t1.30497-1/cp0/c12.0.40.40a/p40x40/84688533_170842440872810_7559275468982059008_n.jpg?_nc_cat=1&ccb=3&_nc_sid=7206a8&_nc_ohc=ucBZ3Qt1MvkAX_Bzo9K&_nc_ht=scontent-nrt1-1.xx&tp=27&oh=0c1968e06cbf7cd964e7d3784f83085f&oe=6064714E'
                        }
                      />
                      <div className='div_ModalContents_body_header_'>
                        <span>{user.userData.user_name}</span>
                        <select defaultValue=''>
                          {PrivateOptions.map((item, index) => (
                            <option key={index} value={`${item.value}`}>
                              {item.label}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>

                    <div className='div_ModalContents_body_text'>
                      {user && (
                        <textarea
                          placeholder={`${user.userData.user_name}     ?`}
                          onChange={onDescriptionChange}
                          value={Description}
                        ></textarea>
                      )}
                      <div className='div_ModalContents_body_displayFiles'>
                        {Thumbnail !== '' && (
                          <div
                            className='div_Thumbnail'
                            style={{
                              borderRadius: '6px',
                              border: '2px solid #f0f2f5',
                              display: 'flex',
                              width: '100%',
                              height: '100%',
                              alignItems: 'center',
                              justifyContent: 'center',
                            }}
                          >
                            <img
                              // src={`http://localhost:5000/${Thumbnail}`}
                              src={`https://www.ftclone.com/${Thumbnail}`}
                              alt='displayThumbnails'
                            />
                          </div>
                        )}
                        {ResizedFilePath !== '' && (
                          <div
                            className='div_ResizedImage'
                            style={{
                              borderRadius: '6px',
                              border: '2px solid #f0f2f5',
                              display: 'flex',
                              width: '100%',
                              height: '100%',
                              alignItems: 'center',
                              justifyContent: 'center',
                            }}
                          >
                            <img
                              style={{ width: '100%', height: '100%' }}
                              // src={`http://localhost:5000/${ResizedFilePath}`}
                              src={`https://www.ftclone.com/${ResizedFilePath}`}
                              alt='displayImage'
                            />
                          </div>
                        )}
                      </div>
                      <div className='div_ModalContents_body_files'>
                        <Dropzone
                          style={{
                            position: 'static',
                            height: '20px',
                            minWidth: '100px',
                          }}
                          onDrop={(files) => onDrop(files)}
                          multiple={true}
                          maxSize={800000000}
                        >
                          {({ getRootProps, getInputProps }) => (
                            <div
                              className='div_mainContainer_dropzone_content__'
                              {...getRootProps()}
                            >
                              <input {...getInputProps()} />
                              <span
                                className=''
                                style={{
                                  marginRight: '8px',
                                  fontFamily: 'inherit',
                                  lineHeight: '1.0667',
                                  cursor: 'pointer',
                                  WebkitFontFmoothing: 'antialiased',
                                }}
                              >
                                <VideoCameraFilled
                                  style={{
                                    width: '24px',
                                    height: '24px',
                                    fontSize: '24px',
                                    color: '#f02849',
                                  }}
                                />
                              </span>
                              <span style={{ marginRight: '8px' }}>
                                <PictureFilled
                                  style={{
                                    paddingRight: '8px',
                                    width: '24px',
                                    height: '24px',
                                    fontSize: '24px',
                                    color: '#45bd62',
                                  }}
                                />
                              </span>
                              <span style={{ marginRight: '8px' }}>
                                <SmileOutlined
                                  style={{
                                    width: '24px',
                                    height: '24px',
                                    fontSize: '24px',
                                    color: '#f7b928',
                                  }}
                                />
                              </span>
                            </div>
                          )}
                        </Dropzone>
                      </div>
                      <button
                        type='submit'
                        onClick={onSubmit}
                        style={{
                          color: '#ffffff',
                          borderRadius: '6px',
                          fontFamily:
                            'Segoe UI Historic, Segoe UI, Arial, Helvetica, sans-serif;',
                          fontSize: '0.925rem',
                          fontWeight: '560',
                          lineHeight: '1.0667',
                          cursor: 'pointer',
                          WebkitFontFmoothing: 'antialiased',
                          borderColor: '#1877f2',
                          background: '#1877f2',
                          height: 'auto',
                          marginTop: '40px',
                          marginBottom: '0px',
                        }}
                      >
                        
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}

      <div className='div_uploadUnit'>
        <div className='div_uploadUnit_'>
          <div className='div_uploadUnit_inputs'>
            <div className='div_uploadUnit_inputs_'>
              <div className='div_uploadUnit_inputs_Avatar'>
                <Avatar
                  style={{ cursor: 'pointer', width: '100%', height: '100%' }}
                  src={
                    'http://scontent-nrt1-1.xx.fbcdn.net/v/t1.30497-1/cp0/c12.0.40.40a/p40x40/84688533_170842440872810_7559275468982059008_n.jpg?_nc_cat=1&ccb=3&_nc_sid=7206a8&_nc_ohc=ucBZ3Qt1MvkAX_Bzo9K&_nc_ht=scontent-nrt1-1.xx&tp=27&oh=0c1968e06cbf7cd964e7d3784f83085f&oe=6064714E'
                  }
                >
                  <a href='#' role='link'></a>
                </Avatar>
              </div>
              <div className='div_uploadUnit_inputs_text'>
                <span
                  onClick={() => {
                    uploadOnClickHandler();
                  }}
                >
                  {user.userData.isAuth
                    ? `${user.userData.user_name}    ?`
                    : ' .'}
                </span>
              </div>
            </div>
          </div>
          <div className='div_uploadUnit_files'>
            <div className='div_uploadUnit_files_liveBroadcast'>
              <div className='div_uploadUnit_files_liveBroadcast_'>
                <span
                  className='liveIcon'
                  //   style={{
                  //     marginRight: '8px',
                  //     fontFamily: 'inherit',
                  //     lineHeight: '1.0667',
                  //     cursor: 'pointer',
                  //     WebkitFontFmoothing: 'antialiased',
                  //   }}
                >
                  <VideoCameraFilled
                    style={{
                      marginRight: '8px',
                      width: '100%',
                      height: '100%',
                      fontSize: '1.5em',
                      color: '#f02849',
                    }}
                  />
                </span>
                <span
                  onClick={() => {
                    uploadOnClickHandler();
                  }}
                  style={{
                    fontFamily:
                      'Segoe UI Historic, Segoe UI, Arial, Helvetica, sans-serif',
                    wordBreak: 'break-word',
                    color: '#65676B',
                    fontWeight: '600',
                    fontSize: '1.0em',
                    lineHeight: '1.3333em',
                    cursor: 'pointer',
                  }}
                >
                   
                </span>
              </div>
            </div>
            <div className='div_uploadUnit_files_photosNvideos'>
              <div className='div_uploadUnit_files_photosNvideos_'>
                <span className='imgIcon'>
                  <PictureFilled
                    style={{
                      marginRight: '8px',
                      width: '100%',
                      height: '100%',
                      fontSize: '1.5em',
                      color: '#45bd62',
                    }}
                  />
                </span>
                <span
                  onClick={() => {
                    uploadOnClickHandler();
                  }}
                  style={{
                    fontFamily:
                      'Segoe UI Historic, Segoe UI, Arial, Helvetica, sans-serif',
                    wordBreak: 'break-word',
                    color: '#65676B',
                    fontWeight: '600',
                    fontSize: '1.0em',
                    lineHeight: '1.3333',
                    cursor: 'pointer',
                  }}
                >
                  /
                </span>
              </div>
            </div>
            <div className='div_uploadUnit_files_broadcast_feelingsNactivities'>
              <div className='div_uploadUnit_files_broadcast_feelingsNactivities_'>
                <span className='emotionIcon'>
                  <SmileOutlined
                    style={{
                      marginRight: '8px',
                      width: '100%',
                      height: '100%',
                      fontSize: '1.5em',
                      color: '#f7b928',
                    }}
                  />
                </span>
                <span
                  onClick={() => {
                    uploadOnClickHandler();
                  }}
                  style={{
                    fontFamily:
                      'Segoe UI Historic, Segoe UI, Arial, Helvetica, sans-serif',
                    wordBreak: 'break-word',
                    color: '#65676B',
                    fontWeight: '600',
                    fontSize: '1.0em',
                    lineHeight: '1.3333',
                    cursor: 'pointer',
                  }}
                >
                  /
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default withRouter(Upload);
